.modal{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 111111;
    .calender{
        position: relative;
        background-color: aliceblue;
        width: 825px;
        height: 500px;
        border-radius: 8px;
        z-index: 111111;
        .exitModalBtn{
            position: absolute;
            right: -1%;
            top: -2%;
            cursor: pointer;
            padding: 10px;
            border-radius: 42% 56% 72% 28% / 42% 42% 56% 48%;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: $primaryTextColor; }
        header{
            width: 825px;
            background-color: #fff;
            padding: 20px;
            border-radius: 8px;
            table{
                th{ color: #A6B5D6; }
                width: 100%;
                .firstRow{ margin-bottom: 20px;     }
                th{   text-align: left; }
                tr{
                    border: 1px solid #111;
                    td{  padding: 10px 0; }}}} }
}

@media (max-width: 835px){
    .modal{
        .calender{
            width: 745px;
            height: 300px;
            bottom: 10%;    
            header{  width: 745px;  } }}
}
@media (max-width: 601px){
    .modal{
        .calender{
            width: 545px;
            height: 300px;  
            bottom: 10%;  
            header{
                width: 545px;
                table{
                    th{  font-size: 10px; } } } }}
}
@media (max-width: 450px){
    .modal{
        .calender{
            width: 345px;
            height: 150px;  
            bottom: 20%; 
            header{
                width: 345px;
                table{
                    th{    font-size: 10px;  }
                    tr{   font-size: 10px;   }  } }}}
} 